import uniStatusBar from '../../components/uni-status-bar/uni-status-bar.vue';
import {login} from '../../utils/api/api'
export default {
	components: {
		uniStatusBar
	},
	data() {
		return {
			disabled:true,
			loading:false,
			status:false,
			username:"",
			password:"",
			phone:"",
			checknum:"",
			phonenum: [
				'中国(+86)', 
				'俄罗斯(+7)', 
				'菲律宾(+63)',
				],
		}
	},
	onLoad() {

	},
	watch: {
		username(val){
			this.OnBtnChange();
		},
		password(val){
			this.OnBtnChange();
		},
		phone(val){
			this.OnBtnChange();
		},
		checknum(val){
			this.OnBtnChange();
		}
	},
	methods: {
		// 初始化表单
		initInput(){
			this.username='';
			this.password='';
			this.phone='';
			this.checknum='';
		},
		// 改变按钮状态
		OnBtnChange(){
			if((this.username && this.password)||(this.phone && this.checknum)){
				this.disabled=false;return;
			}
			this.disabled=true
		},
		
		// 登录状态切换
		changeStatus(){
			this.initInput();
			this.status =!this.status;
		},
		back(){
			console.log("返回上一步")
			
		},
		submit(){
			uni.showLoading();
			// setTimeout(function () {
			// 	uni.hideLoading();
			// }, 6000);
			console.log("点击")
			const param = { username: this.phone, password: this.password };
			console.log("点击2")
			this.$postRequest(login, param).then(res => {
				console.log("点击3")
			  console.log(param, res)
			  if (res.status == 200) {
				if (res.data.errCode == 0) {
				  this.$store.commit(types.LOGIN, res.data.data.sign);				
				  uni.hideLoading();
				  uni.showToast({
				  	title: "登录成功",
				  	icon: "success"
				  })
				  uni.navigateTo({
				  	url: '../index/index',
				  });
				} else {
					uni.hideLoading();
					uni.showToast({
					title: res.data.errMsg,
					icon: "none"
					})
				}
			  }
			})
		},
		PhoneChange(e) {
			this.index = e.detail.value
		},
		toRegister(){
			uni.navigateTo({
				url: 'register',
			});
		},
		toResetpasswd(){
			uni.navigateTo({
				url: 'resetpasswd',
			});
		},			
	}
}
